export const SET_MUSIC_LIST = 'social-network/music/SET_MUSIC_LIST'
export const REMOVED_MUSIC_ITEM = 'social-network/music/REMOVED_MUSIC_ITEM'
export const ADD_MUSIC_ITEM_TO_QUEUE = 'social-network/music/ADD_MUSIC_ITEM_TO_QUEUE'

export type MusicItem = {
  id: number
  name: string
  audioUrl: string
  pictureUrl: string | null
}

export type SetMusicListActionType = {
  type: typeof SET_MUSIC_LIST
  musicList: MusicItem[]
}

export type RemovedMusicItemActionType = {
  type: typeof REMOVED_MUSIC_ITEM
  musicItemId: number
}

export type AddMusicItemToQueueActionType = {
  type: typeof ADD_MUSIC_ITEM_TO_QUEUE
  musicItemId: number
}

export type CombinedMusicActionType =
  | SetMusicListActionType
  | RemovedMusicItemActionType
  | AddMusicItemToQueueActionType
