import { AppThunk } from '../../store'

import { musicAPI } from '../../../dal/localForage'

import * as actions from './actions'

export const getMusicList = (): AppThunk => async dispatch => {
  const blob = window.URL || window.webkitURL

  let response = await musicAPI.getMusicListFromLF()
  if (response) {
    dispatch(
      actions.setMusicList(
        response.map(item => ({
          id: item.id,
          name: item.name,
          audioUrl: blob.createObjectURL(item.audioFile),
          pictureUrl:
            item.picture === null ? null : blob.createObjectURL(item.picture),
        }))
      )
    )
  }
}

export const saveMusicFile =
  (name: string, audioFile: File, picture: File | null = null): AppThunk =>
  async dispatch => {
    await musicAPI.saveAudioToLF(Date.now(), name, audioFile, picture)
    dispatch(getMusicList())
  }

export const removeMusicItem =
  (id: number): AppThunk =>
  async dispatch => {
    await musicAPI.removeAudioFromLF(id)
    dispatch(actions.removeMusicItem(id))
  }
