import * as types from './types'

export const setMusicList = (
  musicList: types.MusicItem[]
): types.SetMusicListActionType => ({
  type: types.SET_MUSIC_LIST,
  musicList,
})

export const removeMusicItem = (
  id: number
): types.RemovedMusicItemActionType => ({
  type: types.REMOVED_MUSIC_ITEM,
  musicItemId: id,
})

export const addMusicItemToQueue = (id: number): types.AddMusicItemToQueueActionType => ({
  type: types.ADD_MUSIC_ITEM_TO_QUEUE,
  musicItemId: id,
})
