import { MusicItem } from './responsesTypes'
import localforage from 'localforage'

export const themeAPI = {
  setAppThemeToLF: async (value: 'light' | 'dark') => {
    return await localforage.setItem('app-theme', value)
  },

  getAppThemeFromLF: async () => {
    return await localforage.getItem<'light' | 'dark'>('app-theme')
  },
}

export const musicAPI = {
  saveAudioToLF: async (
    id: number,
    name: string,
    audioFile: File,
    picture: File | null = null
  ) => {
    const musicList = await localforage.getItem<MusicItem[]>('musicList')

    if (!musicList) {
      return await localforage.setItem('musicList', [
        { id, name, audioFile, picture },
      ])
    }

    return await localforage.setItem('musicList', [
      ...musicList,
      { id, name, audioFile, picture },
    ])
  },

  removeAudioFromLF: async (id: number) => {
    const musicList = await localforage.getItem<MusicItem[]>('musicList')

    return await localforage.setItem(
      'musicList',
      musicList?.filter(item => item.id !== id)
    )
  },

  getMusicListFromLF: async () => {
    return await localforage.getItem<MusicItem[]>('musicList')
  },
}
